import {createStore} from "redux";

//Actions
function receiveData(data) {
  return{
    type: 'RECEIVE_DATA',
    data: data
  }
}

export const fetchDataa = () => {
  return (dispatch, getState) => {
    const url = '//you url'
    fetch(url)
      .then(res => {
        dispatch(receiveData(res.data))
      })
      .catch(function (error) {
        console.log(error);
      });
  }
}

export const fetchData = (data) => {
  return {
    type: 'FETCH_SUCCESS',
    payload: {
      user_data: data.user_data,
      audio_features: data.audio_features,
      tracks_data: data.tracks_data,
      tracks_popularity: data.tracks_popularity,
      tracks_collage: data.tracks_collage,
      artists_data: data.artists_data,
      artists_popularity: data.artists_popularity,
      artists_collage: data.artists_collage
    }
  }
}


//Reducers
const initialState = {
  data: [],
  isLoading: true
};

export const rootReducer = (state = initialState, action) => {
  switch (action.type) {

    case 'RECEIVE_DATA' :
      return Object.assign({}, ...state, {
        articles: state.articles.concat(action.payload)
      });

    default:
      return state
  }
};


//Store
export const store = createStore(
  rootReducer,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);



